export class CosmicTheme {
  constructor() {
    this.isInitialized = false;
    this.settings = {
      accentColor: 'neutral', //   
      animationSpeed: 'normal', //    
      starDensity: 'medium', //  
      starShape: 'circle', //   
    };
  }

  /**
   *       .
   */
  initialize() {
    if (!this.isInitialized) {
      console.log('CosmicTheme has been initialized.');
      this.isInitialized = true;
    }
  }

  /**
   *     .
   */
  applyTheme() {
    console.log('Applying theme with settings:', this.settings);

    //   CSS  
    document.body.style.setProperty('--star-shape', this.getStarShape()); //   
    document.body.style.setProperty('--bg-primary', this.getBackgroundColor()); //    
    document.body.style.setProperty('--bg-secondary', this.getSecondaryBackgroundColor()); //    
    document.body.style.setProperty('--animation-speed', this.getAnimationSpeed()); //   
    document.body.style.setProperty('--star-density', this.getStarDensity()); //   
  }

  /**
   *         .
   * @returns {string}  HEX -  
   */
  getBackgroundColor() {
    const colorMap = {
      cheerful: '#ffecd2', //  
      warm: '#ffe4b5', //  
      calm: '#e6f7ff', //  
      curious: '#f5e6ff', //  
      neutral: '#f8f9fa', //  
      reassuring: '#e8ffe8', //  
      friendly: '#fffacd', //  
    };
    return colorMap[this.settings.accentColor] || '#ffffff'; //  
  }

  /**
   *         .
   * @returns {string}  HEX -  
   */
  getSecondaryBackgroundColor() {
    const secondaryColorMap = {
      cheerful: '#ffe8b0',
      warm: '#ffd37b',
      calm: '#d9f0f8',
      curious: '#e2d3f5',
      neutral: '#e6e6e6',
      reassuring: '#d4f7d4',
      friendly: '#fff799',
    };
    return secondaryColorMap[this.settings.accentColor] || '#e0e0e0'; //  
  }

  /**
   *      .
   * @returns {string}  
   */
  getStarShape() {
    const shapeMap = {
      khaliji: 'triangle', //    
      shami: 'star', //  
      masri: 'diamond', //    
      fusha: 'circle', //    
    };
    return shapeMap[this.settings.accentColor] || 'circle'; //  
  }

  /**
   *      .
   * @returns {string}  (, , )
   */
  getAnimationSpeed() {
    //         
    if (this.settings.animationSpeed) {
      return this.settings.animationSpeed;
    }

    //      
    const speedMap = {
      cheerful: 'fast',
      warm: 'normal',
      calm: 'slow',
      curious: 'normal',
      neutral: 'normal',
      reassuring: 'slow',
      friendly: 'fast',
    };
    return speedMap[this.settings.accentColor] || 'normal'; //  
  }

  /**
   *      .
   * @returns {string}  (, , )
   */
  getStarDensity() {
    const densityMap = {
      cheerful: 'high',
      warm: 'medium',
      calm: 'low',
      curious: 'medium',
      neutral: 'medium',
      reassuring: 'low',
      friendly: 'high',
    };
    return densityMap[this.settings.accentColor] || 'medium'; //  
  }
}

// Create a singleton instance
const cosmicTheme = new CosmicTheme();

/**
 * Initialize the cosmic theme
 */
export function initializeCosmicTheme() {
  document.body.classList.add('cosmic-theme');
  cosmicTheme.initialize();
  cosmicTheme.applyTheme();
  console.log("Cosmic theme initialized!");

  // Make the theme available globally for legacy code
  window.cosmicTheme = cosmicTheme;
}

/**
 * Get the cosmic theme instance
 * @returns {CosmicTheme} The cosmic theme instance
 */
export function getCosmicTheme() {
  return cosmicTheme;
}

